import request from '@/plugin/axios';
import publicService from "@/api/sys/service.js";

const service = {
    ...publicService,
    /**
     * 分页获取新经济组织列表
     * 
     * @param {any} param 分页参数
     * @param {any} successFunc 回调函数
     */
    requestPageUnpublicEco(param, successFunc) {
        request({
            url: "/compre/group/unpubliceco/findPage?name="+ param.queryForm.name +"&districtCode="+ param.queryForm.districtCode +"&page="+ param.page +"&pageSize="+ param.pageSize,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 添加新经济组织
     * 
     * @param {any} param 新经济组织参数
     * @param {any} successFunc 回调函数
     */
    requestSaveUnpublicEco(param, successFunc) {
        request({
            url: "/compre/group/unpubliceco/save",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 修改新经济组织
     * 
     * @param {any} param 新经济组织参数
     * @param {any} successFunc 回调函数
     */
    requestUpdateUnpublicEco(param, successFunc) {
        request({
            url: "/compre/group/unpubliceco/update",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 删除新经济组织
     * 
     * @param {any} id 新经济组织 ID
     * @param {any} successFunc 回调函数
     */
    requestDeleteUnpublicEco(id, successFunc) {
        request({
            url: "/compre/group/unpubliceco/delete?id="+ id,
            method: "post",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 分页获取新社会组织列表
     * 
     * @param {any} param 分页参数
     * @param {any} successFunc 回调函数
     */
    requestPageGroupSocial(param, successFunc) {
        request({
            url: "/compre/group/social/findPage?name="+ param.queryForm.name +"&districtCode="+ param.queryForm.districtCode +"&page="+ param.page +"&pageSize="+ param.pageSize,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 添加新社会组织
     * 
     * @param {any} param 新社会组织参数
     * @param {any} successFunc 回调函数
     */
    requestSaveGroupSocial(param, successFunc) {
        request({
            url: "/compre/group/social/save",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 修改新社会组织
     * 
     * @param {any} param 新社会组织参数
     * @param {any} successFunc 回调函数
     */
    requestUpdateGroupSocial(param, successFunc) {
        request({
            url: "/compre/group/social/update",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 删除新社会组织
     * 
     * @param {any} id 新社会组织 ID
     * @param {any} successFunc 回调函数
     */
    requestDeleteGroupSocial(id, successFunc) {
        request({
            url: "/compre/group/social/delete?id="+ id,
            method: "post",
        }).then((data) => {
            successFunc(data);
        })
    }
};

export default service;