<template>
    <div class="charts-page">
        <div class="top-card">
            <techo-card resizeEvent="topCard" @onUpLevel="handleUpLevel" :maxCard="true" :upLevel="isShowLevelBtu">
                <div slot="header">新社会组织</div>
                <div id="TopBar" style="height:100%"></div>
            </techo-card>
        </div>
        <el-row class="bottom-card" :gutter="10">
            <el-col :span="8">
                <techo-card resizeEvent="leftCard" :maxCard="true">
                    <div slot="header">组织类别</div>
                    <div id="LeftPie" style="height:100%"></div>
                </techo-card>
            </el-col>
            <el-col :span="8">
                <techo-card resizeEvent="MiddleCard" :maxCard="true">
                    <div slot="header">状态</div>
                    <div id="MiddlePie" style="height:100%"></div>
                </techo-card>
            </el-col>
            <el-col :span="8">
                <techo-card resizeEvent="rightCard" :maxCard="true">
                    <div slot="header">关注程度</div>
                    <div id="RightPie" style="height:100%"></div>
                </techo-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import barOptions from "./config/barOptions.js";
import pieOptions from "./config/pieOptions.js";
import service from "./services/double_origanization_service.js";
export default {
  data() {
    return {
      DISCODE: "6102",
      // 新社会组织分布
      topBarOptions: {},
      topBarChart: null,
      // 组织类别比例占比
      leftPieOptions: {},
      leftPieChart: null,
      // 状态比例占比
      middlePieOptions: {},
      middlePieChart: null,
      // 关注程度占比
      rightPieOptions: {},
      rightPieChart: null,
      // 当前柱状图级别code
      byCode: "",
      isShowLevelBtu: true,
      COLORS: ["#3965c3", "#ffa639", "#f75a51"]
    };
  },
  watch: {
    byCode(value) {
      this.isShowLevelBtu = value.length == 6 ? false : true;
    }
  },
  mounted() {
    let that = this;
    // 新社会组织分布
    this.topBarChart = this.$echarts.init(this.$el.querySelector("#TopBar"));
    // 组织类别比例占比
    this.leftPieChart = this.$echarts.init(this.$el.querySelector("#LeftPie"));
    // 状态比例占比
    this.middlePieChart = this.$echarts.init(
      this.$el.querySelector("#MiddlePie")
    );
    // 关注程度占比
    this.rightPieChart = this.$echarts.init(
      this.$el.querySelector("#RightPie")
    );
    this.topBarChart.on("click", val => {
      if (this.byCode.length != 14) {
        this.getOrgData(val.data.rawdata.code);
        this.getTypePieData(val.data.rawdata.code);
        this.getStatusPieData(val.data.rawdata.code);
        this.getDegreePieData(val.data.rawdata.code);
      }
    });
    // 初始化统计图自适应函数
    this.$bus.$on("topCard", () => {
      that.topBarChart.resize();
    });
    this.$bus.$on("leftCard", () => {
      that.leftPieChart.resize();
    });
    this.$bus.$on("MiddleCard", () => {
      that.middlePieChart.resize();
    });
    this.$bus.$on("rightCard", () => {
      that.rightPieChart.resize();
    });
  },
  created() {
    this.getOrgData();
    this.getTypePieData();
    this.getStatusPieData();
    this.getDegreePieData();
  },
  methods: {
    // 新社会组织
    getOrgData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityNames: "ViewGroupSocial"
      };
      let that = this;
      this.topBarOptions = _.cloneDeep(barOptions);
      service.requestByEntityNames(param, data => {
        if (data.length == 0) return;
        this.byCode = data[0].code;
        this.topBarOptions.yAxis.name = "数量";
        this.topBarOptions.legend.data = ["数量"];
        this.topBarOptions.series.push({
          name: "数量",
          type: "bar",
          itemStyle: {
            normal: {
              color: that.COLORS[Math.floor(Math.random() * 3)]
            }
          },
          barMaxWidth: 80,
          data: []
        });
        _.map(data, (value, index) => {
          that.topBarOptions.xAxis.data.push(value.name);
          this.topBarOptions.series[0].data.push({
            rawdata: value,
            value: value.count
          });
        });
        this.topBarChart.setOption(that.topBarOptions);
        this.topBarChart.resize();
      });
    },
    // 组织类别
    getTypePieData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "ViewGroupSocial",
        fieldName: "socialType"
      };
      let that = this;
      this.leftPieOptions = _.cloneDeep(pieOptions);
      service.requestByFieldName(param, data => {
        that.leftPieOptions.legend.data = _.keys(_.groupBy(data, "name"));
        that.leftPieOptions.title.text = "组织类别";
        that.leftPieOptions.series[0].name = "新社会组织";
        _.map(data, item => {
          that.leftPieOptions.series[0].data.push({
            name: item.name,
            value: item.count
          });
        });
        that.leftPieOptions.series[0].radius = "45%";
        that.leftPieOptions.series[0].center = ["70%", "60%"];
        that.leftPieChart.setOption(that.leftPieOptions);
        that.leftPieChart.resize();
      });
    },
    // 状态
    getStatusPieData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "ViewGroupSocial",
        fieldName: "status"
      };
      let that = this;
      this.middlePieOptions = _.cloneDeep(pieOptions);
      service.requestByFieldName(param, data => {
        data = data.map(item => {
          item.name = item.name == 1 ? "正常" : "注销";
          return item;
        });
        that.middlePieOptions.legend.data = _.keys(_.groupBy(data, "name"));
        that.middlePieOptions.title.text = "状态";
        that.middlePieOptions.series[0].name = "新社会组织";
        _.map(data, item => {
          that.middlePieOptions.series[0].data.push({
            name: item.name,
            value: item.count
          });
        });
        that.middlePieOptions.series[0].radius = "45%";
        that.middlePieOptions.series[0].center = ["70%", "60%"];
        that.middlePieChart.setOption(that.middlePieOptions);
        that.middlePieChart.resize();
      });
    },
    // 关注程度
    getDegreePieData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "ViewGroupSocial",
        fieldName: "concernedDegree"
      };
      let that = this;
      this.rightPieOptions = _.cloneDeep(pieOptions);
      service.requestByFieldName(param, data => {
        that.rightPieOptions.legend.data = _.keys(_.groupBy(data, "name"));
        that.rightPieOptions.title.text = "关注程度";
        that.rightPieOptions.series[0].name = "新社会组织";
        _.map(data, item => {
          that.rightPieOptions.series[0].data.push({
            name: item.name,
            value: item.count
          });
        });
        that.rightPieOptions.series[0].radius = "45%";
        that.rightPieOptions.series[0].center = ["70%", "60%"];
        that.rightPieChart.setOption(that.rightPieOptions);
        that.rightPieChart.resize();
      });
    },
    /** 返回上一级 */
    handleUpLevel() {
      let districtCode = "";
      switch (this.byCode.length) {
        case 9:
          districtCode = this.byCode.slice(0, 4);
          break;
        case 12:
          districtCode = this.byCode.slice(0, 6);
          break;
        case 14:
          districtCode = this.byCode.slice(0, 9);
          break;
        default:
          break;
      }
      this.getOrgData(districtCode);
      this.getTypePieData(districtCode);
      this.getStatusPieData(districtCode);
      this.getDegreePieData(districtCode);
    }
  }
};
</script>

<style lang="scss">
.charts-page {
  padding: 10px 5px;
  .top-card {
    height: 23rem;
  }
  .bottom-card {
    padding-top: 0.6rem;
    position: absolute;
    height: calc(100% - 24.2rem);
    width: 100%;
    .el-col-8 {
      height: 100%;
    }
  }
}
</style>